<template>
  <div>
    <el-button type="default" @click="handleClickRefresh"
      ><el-icon><Refresh /></el-icon>刷新</el-button
    >
    <el-button type="primary" @click="handleClickInsert"
      ><el-icon><Plus /></el-icon>添加</el-button
    >
  </div>
</template>

<script setup>
import { Plus, Refresh } from "@element-plus/icons-vue";
</script>


<script>
export default {
  name: "IntentToolbar",

  mounted() {
    this.fetch();
  },

  methods: {
    fetch() {
      this.$store.dispatch("intent/fetch");
    },

    handleClickRefresh() {
      this.fetch();
    },

    handleClickInsert() {
      this.fireCommand("insert");
    },

    fireCommand(name) {
      this.$emit("command", { name });
    },
  },
};
</script>
