<template>
  <div class="client">
    <h1>命令模板</h1>
    <MyToolbar @command="handleCommand" />
    <MyTable />
    <MyEditor :intent="intentEditor" />
  </div>
</template>

<script>
import MyTable from "./table.vue";
import MyToolbar from "./toolbar.vue";
import MyEditor from "./editor.vue";

import Intent from "./../../js/intent.js";

export default {
  name: "IntentIndex",

  components: { MyEditor, MyTable, MyToolbar },

  data() {
    return {
      items: [],
      intentEditor: {},
    };
  },

  methods: {
    handleCommand(cmd) {
      let name = cmd.name;
      if (name == "insert") {
        this.doInsertItem();
      } else if (name == "edit") {
        this.doEditItem();
      } else {
        console.warn("bad command name:" + name);
      }
    },

    doEditItem() {
      let i = Intent.NewIntent(this, {});
      this.intentEditor = i;
    },

    doInsertItem() {
      let action = "insert";
      let data = {};
      let i = Intent.NewIntent(this, { action, data });
      this.intentEditor = i;
    },
  },
};
</script>

<style scoped>
.client {
  text-align: left;
}
</style>
