


function Intent(context, p) {
    this.context = context
    this.date = new Date()
    if (p != null) {
        this.action = p.action;
        this.data = p.data;
        this.location = p.location;
        this.params = p.params
        this.sender = p.sender;
    }
}


Intent.prototype = {

}

export default {
    name: "Intent",

    NewIntent(context, p) {
        return new Intent(context, p)
    }

}
