<template>
  <div>
    <el-dialog v-model="showDialog" :title="title">
      <el-form label-width="200px">
        <el-form-item label="aaa"></el-form-item>
        <el-form-item></el-form-item>
      </el-form>
      <div>
        <el-button type="success">保存</el-button>
        <el-button type="default" @click="showDialog = false">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "ProjectEditor",

  props: {
    intent: Object,
  },

  data() {
    return {
      item: {},
      showDialog: false,
      title: "[untitle]",
    };
  },

  watch: {
    intent(value) {
      let action = value.action;
      if (action == "insert") {
        this.insertNewItem(value.data);
      } else if (action == "edit") {
        this.editItem();
      }
    },
  },

  methods: {
    insertNewItem(item) {
      this.item = item;
      this.title = "添加命令模板";
      this.showDialog = true;
    },

    editItem(item) {
      this.item = item;
      this.title = "编辑命令模板";
      this.showDialog = true;
    },
  },
};
</script>
